import { Component, ViewChild } from '@angular/core';
import { Platform, Nav, MenuController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ThreeDeeTouch, ThreeDeeTouchQuickAction, ThreeDeeTouchForceTouch } from '@ionic-native/three-dee-touch';
import { PlayerServiceProvider } from './../providers/player-service/player-service';
import firebase from 'firebase';

import { HomePage } from '../pages/home/home';
import { AboutPage } from '../pages/about/about';
import { SocialPage } from '../pages/social/social';
import { LineUpPage } from '../pages/line-up/line-up';
import { DjPage } from '../pages/dj/dj';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage:any = HomePage;
  
  // Insert the pages you want to navigate to on the side-menu here, dont forget to also import!
  aboutPage = AboutPage;
  djPage = DjPage;
  homePage = HomePage;
  lineUpPage = LineUpPage;
  socialPage = SocialPage

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, private menuCtrl: MenuController, private threeDeeTouch: ThreeDeeTouch, public playerService: PlayerServiceProvider) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();

      //Firebase configuration - replace the below with your own details from Firebase.
      firebase.initializeApp({
        apiKey: "AIzaSyB3lOGGll1djQ0S8V2UHUIzgS58ylLTQ8w",
        authDomain: "radio-app-stream.firebaseapp.com",
        databaseURL: "https://radio-app-stream.firebaseio.com",
        projectId: "radio-app-stream",
        storageBucket: "radio-app-stream.appspot.com",
        messagingSenderId: "910152064891"
      });

      // OneSignal Push Notification Service Configuration
      var notificationOpenedCallback = function(jsonData) {
        console.log('notificationOpenedCallback: ' + JSON.stringify(jsonData));
        };
    
        window["plugins"].OneSignal
        .startInit("YOUR_APPID", "YOUR_GOOGLE_PROJECT_NUMBER_IF_ANDROID")
        .handleNotificationOpened(notificationOpenedCallback)
        .endInit();


      //ThreeD Touch Functionality
      this.threeDeeTouch.isAvailable().then(isAvailable => console.log('3D Touch available? ' + isAvailable));
      
      this.threeDeeTouch.watchForceTouches()
        .subscribe(
          (data: ThreeDeeTouchForceTouch) => {
            console.log('Force touch %' + data.force);
            console.log('Force touch timestamp: ' + data.timestamp);
            console.log('Force touch x: ' + data.x);
            console.log('Force touch y: ' + data.y);
          }
        );
      
      
      let actions: Array<ThreeDeeTouchQuickAction> = [
        {
          type: 'socialMedia',
          title: 'Social Media',
          subtitle: 'Connect With Us!',
          iconType: 'Message'
        },
        {
          type: 'dj',
          title: '.radio DJs',
          subtitle: 'View Bios',
          iconType: 'Contact'
        },
        {
          type: 'lineUp',
          title: 'Show Line Up',
          subtitle: 'View Schedule',
          iconType: 'Compose'
        },
        {
          type: 'home',
          title: 'Listen Live',
          subtitle: '.radio stream',
          iconType: 'Play'
        }
      ];
      
      this.threeDeeTouch.configureQuickActions(actions);
      
      this.threeDeeTouch.onHomeIconPressed().subscribe(
       (payload) => {
         // returns an object that is the button you presed
         if (payload.type == 'socialMedia') {
            this.nav.setRoot(SocialPage);
         } else if (payload.type =='dj') {
            this.nav.setRoot(DjPage);
         } else if (payload.type =='lineUp') {
            this.nav.setRoot(LineUpPage);
         } else if (payload.type =='home') {
            this.playerService.playAudio();
         };

         console.log('Pressed the ${payload.title} button')
         console.log(payload.type)

       }
      )

    });
  }

      // Navigation functions for the side-menu pages
      about() {
      this.nav.setRoot(AboutPage);
      this.menuCtrl.close();
      }

      lineUp() {
      this.nav.setRoot(LineUpPage);
      this.menuCtrl.close();
      }

      dj() {
      this.nav.setRoot(DjPage);
      this.menuCtrl.close();
      }

      social() {
      this.nav.setRoot(SocialPage);
      this.menuCtrl.close();
      }

      home() {
      this.nav.setRoot(HomePage);
      this.menuCtrl.close();
      }

}




