import { Injectable } from '@angular/core';
import { Media, MediaObject } from '@ionic-native/media';
import { LoadingController } from 'ionic-angular';

@Injectable()
export class PlayerServiceProvider {

  // This controls the alternating play and pause images and function
  play: boolean = true;

  // Property “radio” for the instance of the MediaObject for the Media plugin
  radio: MediaObject

  constructor(private media: Media, public loadingCtrl: LoadingController) {
    
  }

  // Function to play the audio stream
  playAudio() { 
      
    //Loading Spinner Configuration
    const loading = this.loadingCtrl.create({
      content: 'Loading stream...',
      spinner: 'crescent',
      showBackdrop: false,
      cssClass: 'spin' // This class is defined in the app.scss file
      });
    loading.present();

    setTimeout(() => {
      loading.dismiss();
    }, 4000);
    //End of Loading Spinner Configuration
  

    this.radio = this.media.create('http://a.files.bbci.co.uk/media/live/manifesto/audio/simulcast/hls/nonuk/sbr_low/ak/bbc_radio_one.m3u8'); // Insert your own stream URL here.
    this.radio.play();

    // This controls the alternating play and pause images and function
    this.play = false;

    }

  // Function to stop the audio stream
  stopAudio() {
    this.radio.stop();

    // This controls the alternating play and pause images and function
    this.play = true;
    }
  

}



