import { PlayerServiceProvider } from './../../providers/player-service/player-service';
import { Component } from '@angular/core';
import { NavController, Platform, LoadingController } from 'ionic-angular';
import { AdMobFree, AdMobFreeBannerConfig, AdMobFreeInterstitialConfig } from '@ionic-native/admob-free';
import { SocialSharing } from '@ionic-native/social-sharing';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController, public admobFree: AdMobFree, platform: Platform, 
    public loadingCtrl: LoadingController, private socialSharing: SocialSharing, public playerService: PlayerServiceProvider) {
    {

      // Player service property from the player-service.ts provider - controls play and pause in home.html functions
      this.playerService.play

      platform.ready().then(() => {
        // Okay, so the platform is ready and our plugins are available.
        // Here you can do any higher level native things you might need.
        
        // Admob Settings
        const bannerConfig: AdMobFreeBannerConfig = {
          isTesting: true, // Remove in production
          autoShow: true,
          id: 'YOUR APP ID'
        };

        this.admobFree.banner.config(bannerConfig);

        this.admobFree.banner.prepare().then(() => {
          // success
        }).catch(e => console.log(e));
        // End of Admob Settings

        // Remote control Plugin settings
        let artist = "Radio App";
        let title = "All Day Live Radio";
        let album = "Live Stream";
        let image = "http://rockstarmediastore.co.za/radio/remote-logo.png";
        let duration = 0; //use value "1" when playing audio files, leave at "0" for streams.
        let elapsedTime = 1;
    
        var params = [artist, title, album, image, duration, elapsedTime];
        window['remoteControls'].updateMetas((success)=>{
        }, (fail)=>{
        }, params);

      });
    }
        // Play and pause functionality in Notification Center 
        document.addEventListener("remote-event", (event:any) => {
        //do something
        switch(event.remoteEvent.subtype) {
        case "play":
          console.log("PLAY");
          this.playerService.playAudio();
          break;
        case "pause":
          console.log("PAUSE");
          this.playerService.stopAudio();
          break;
          }
        })

  }

    // Social Media Sharing Configurartion
    shareFB() {
      this.socialSharing.shareViaFacebook("Message via Facebook","www/assets/img/socialSharingLogo.png",null /* url */)
      .then(() => {
        console.log('Shared!');
      }).catch((err) => {
        console.log('Oops, something went wrong:', err);
      });
    }

    shareInsta() {
      this.socialSharing.shareViaInstagram("Message via Instagram", "www/assets/img/socialSharingLogo.png")
      .then(() => {
        console.log('Shared!');
      }).catch((err) => {
        console.log('Oops, something went wrong:', err);
      });
    }

    shareTwitter() {
      this.socialSharing.shareViaTwitter("Message via Twitter",null /*Image*/,"http://google.com")
      .then(() => {
        console.log('Shared!');
      }).catch((err) => {
        console.log('Oops, something went wrong:', err);
      });
    }
    
    shareWhatsapp() {
      this.socialSharing.shareViaWhatsApp("Listening to .radio app",null /*Image*/,"http://google.com")
      .then(() => {
        console.log('Shared!');
      }).catch((err) => {
        console.log('Oops, something went wrong:', err);
      });
    }

}


