import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import firebase from 'firebase';

import { HomePage } from '../home/home';
import { ShowPage } from '../show/show';

@IonicPage()
@Component({
  selector: 'page-line-up',
  templateUrl: 'line-up.html',
})
export class LineUpPage {

  // Properties used for Firebase database links
  mon = [];
  tue = [];
  wed = [];
  thu = [];
  fri = [];
  sat = [];
  sun = [];

  //homePage import for navigation back to the homepage
  homePage = HomePage;

  // string for ngModel, ngSwitch, ngSwitchCase & getDay functions  
  days: string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController) {
    
    // Weekly show Firebase data feed configuration. //
    
    // Monday line-up
    firebase.database().ref('shows/mon').on('value', snapshot => {
      this.mon = snapshot.val();
    });
    
    // Tuesday line-up
    firebase.database().ref('shows/tue').on('value', snapshot => {
      this.tue = snapshot.val();
    });

    // Wednesday line-up
    firebase.database().ref('shows/wed').on('value', snapshot => {
      this.wed = snapshot.val();
    });

    // Thursday line-up
    firebase.database().ref('shows/thu').on('value', snapshot => {
      this.thu = snapshot.val();
    });

    // Friday line-up
    firebase.database().ref('shows/fri').on('value', snapshot => {
      this.fri = snapshot.val();
    });

    // Saturday line-up
    firebase.database().ref('shows/sat').on('value', snapshot => {
      this.sat = snapshot.val();
    });

    // Sunday line-up
    firebase.database().ref('shows/sun').on('value', snapshot => {
      this.sun = snapshot.val();
    });

    // getDay Method
    let currentDate = new Date();
    let weekdays = ["sunday", "monday", "tuesday", "wednesday", "thursday", "friday", "saturday"];
    this.days = weekdays[currentDate.getDay()];
  }

  
  // modal controller configuration for show details
  showInfo(event, item) {
    let modal = this.modalCtrl.create(ShowPage, {item: item});
    modal.present();
  } 

}



